<template>
	<view class="content">
		<!-- Êî∂Ë¥ßÂú∞ÂùÄ -->
		<view class="address-section">
			<view class="section-title">Êî∂Ë¥ßÂú∞ÂùÄ</view>
			<view class="address-card" @click="selectAddress">
				<view v-if="selectedAddress">
					<view class="address-info">
						<text class="contact-name">{{selectedAddress.name}}</text>
						<text class="contact-phone">{{selectedAddress.phone}}</text>
					</view>
					<text class="address-detail">{{selectedAddress.address}}</text>
				</view>
				<view v-else class="no-address">
					<text class="no-address-text">ËØ∑ÈÄâÊã©Êî∂Ë¥ßÂú∞ÂùÄ</text>
				</view>
				<text class="address-arrow">></text>
			</view>
		</view>
		
		<!-- ÂïÜÂìÅÂàóË°® -->
		<view class="products-section">
			<view class="section-title">ÂïÜÂìÅ‰ø°ÊÅØ</view>
			<view class="product-list">
				<view class="product-item" v-for="(item, index) in orderItems" :key="index">
					<image :src="item.image" class="product-image" mode="aspectFill"></image>
					<view class="product-info">
						<text class="product-name">{{item.name}}</text>
						<text class="product-spec" v-if="item.specName">ËßÑÊ†ºÔºö{{item.specName}}</text>
						<view class="product-price-section">
							<text class="product-price">¬•{{item.price}}</text>
							<text class="product-quantity">x{{item.quantity}}</text>
						</view>
					</view>
				</view>
			</view>
		</view>
		
		<!-- ÈÖçÈÄÅÊñπÂºè -->
		<view class="delivery-section">
			<view class="section-title">ÈÖçÈÄÅÊñπÂºè</view>
			<view class="delivery-options">
				<view 
					class="delivery-option" 
					v-for="(option, index) in deliveryOptions" 
					:key="index"
					:class="{ active: selectedDelivery === option.value }"
					@click="selectDelivery(option)"
				>
					<text class="delivery-name">{{option.name}}</text>
					<text class="delivery-fee">{{option.fee > 0 ? '¬•' + option.fee : 'ÂÖçË¥π'}}</text>
				</view>
			</view>
		</view>
		
		<!-- ÊîØ‰ªòÊñπÂºè -->
		<view class="payment-section">
			<view class="section-title">ÊîØ‰ªòÊñπÂºè</view>
			<view class="payment-options">
				<view 
					class="payment-option" 
					v-for="(option, index) in paymentOptions" 
					:key="index"
					:class="{ active: selectedPayment === option.value }"
					@click="selectPayment(option)"
				>
					<text class="payment-icon">{{option.icon}}</text>
					<text class="payment-name">{{option.name}}</text>
					<text class="payment-arrow" :class="{ selected: selectedPayment === option.value }">‚úì</text>
				</view>
			</view>
		</view>
		
		<!-- ËÆ¢ÂçïÂ§áÊ≥® -->
		<view class="remark-section">
			<view class="section-title">ËÆ¢ÂçïÂ§áÊ≥®</view>
			<textarea 
				class="remark-input" 
				v-model="orderRemark" 
				placeholder="ËØ∑ËæìÂÖ•ËÆ¢ÂçïÂ§áÊ≥®ÔºàÈÄâÂ°´Ôºâ" 
				maxlength="200"
			></textarea>
		</view>
		
		<!-- ‰ª∑Ê†ºÊòéÁªÜ -->
		<view class="price-section">
			<view class="price-item">
				<text>ÂïÜÂìÅÊÄª‰ª∑</text>
				<text class="price-value">¬•{{totalPrice}}</text>
			</view>
			<view class="price-item" v-if="deliveryFee > 0">
				<text>ÈÖçÈÄÅË¥π</text>
				<text class="price-value">¬•{{deliveryFee}}</text>
			</view>
			<view class="price-item total">
				<text>ÂÆû‰ªòÈáëÈ¢ù</text>
				<text class="final-price">¬•{{finalPrice}}</text>
			</view>
		</view>
		
		<!-- Â∫ïÈÉ®ÁªìÁÆóÊ†è -->
		<view class="bottom-bar">
			<view class="price-summary">
				<text class="summary-label">ÂêàËÆ°Ôºö</text>
				<text class="summary-price">¬•{{finalPrice}}</text>
			</view>
			<button class="pay-btn" @click="submitOrder" :disabled="!canSubmit">Êèê‰∫§ËÆ¢Âçï</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				orderItems: [],
				orderType: 'cart', // cart-Ë¥≠Áâ©ËΩ¶, buy_now-Á´ãÂç≥Ë¥≠‰π∞
				selectedAddress: null,
				selectedDelivery: 'express',
				selectedPayment: 'wechat',
				orderRemark: '',
				deliveryOptions: [
					{ name: 'Âø´ÈÄíÈÖçÈÄÅ', value: 'express', fee: 10 },
					{ name: 'Âà∞Â∫óËá™Êèê', value: 'self_pickup', fee: 0 }
				],
				paymentOptions: [
					{ name: 'ÂæÆ‰ø°ÊîØ‰ªò', value: 'wechat', icon: 'üí≥' },
					{ name: 'ÊîØ‰ªòÂÆù', value: 'alipay', icon: 'üí∞' },
					{ name: '‰ΩôÈ¢ùÊîØ‰ªò', value: 'balance', icon: 'üíé' }
				],
				addressList: [
					{
						id: 1,
						name: 'Âº†‰∏â',
						phone: '13800138000',
						address: 'Âåó‰∫¨Â∏ÇÊúùÈò≥Âå∫Âª∫ÂõΩË∑Ø88Âè∑Áé∞‰ª£ÂüéBÂ∫ß3Â±Ç'
					},
					{
						id: 2,
						name: 'ÊùéÂõõ',
						phone: '13900139000',
						address: 'Âåó‰∫¨Â∏ÇÊµ∑Ê∑ÄÂå∫‰∏≠ÂÖ≥ÊùëÂ§ßË°ó28Âè∑Êµ∑ÈæôÂ§ßÂé¶5Â±Ç'
					}
				]
			}
		},
		computed: {
			// ÂïÜÂìÅÊÄª‰ª∑
			totalPrice() {
				return this.orderItems.reduce((total, item) => {
					return total + (item.price * item.quantity);
				}, 0).toFixed(2);
			},
			// ÈÖçÈÄÅË¥π
			deliveryFee() {
				const delivery = this.deliveryOptions.find(d => d.value === this.selectedDelivery);
				return delivery ? delivery.fee : 0;
			},
			// ÊúÄÁªà‰ª∑Ê†º
			finalPrice() {
				return (parseFloat(this.totalPrice) + this.deliveryFee).toFixed(2);
			},
			// ÊòØÂê¶ÂèØ‰ª•Êèê‰∫§ËÆ¢Âçï
			canSubmit() {
				return this.selectedAddress && this.orderItems.length > 0;
			}
		},
		onLoad(options) {
			if (options.items) {
				try {
					this.orderItems = JSON.parse(decodeURIComponent(options.items));
				} catch (e) {
					console.error('Ëß£ÊûêÂïÜÂìÅÊï∞ÊçÆÂ§±Ë¥•:', e);
				}
			}
			if (options.type) {
				this.orderType = options.type;
			}
			
			// ÈªòËÆ§ÈÄâÊã©Á¨¨‰∏Ä‰∏™Âú∞ÂùÄ
			if (this.addressList.length > 0) {
				this.selectedAddress = this.addressList[0];
			}
		},
		methods: {
			// ÈÄâÊã©Âú∞ÂùÄ
			selectAddress() {
				uni.showActionSheet({
					itemList: this.addressList.map(addr => `${addr.name} ${addr.phone} - ${addr.address}`),
					success: (res) => {
						this.selectedAddress = this.addressList[res.tapIndex];
					}
				});
			},
			
			// ÈÄâÊã©ÈÖçÈÄÅÊñπÂºè
			selectDelivery(option) {
				this.selectedDelivery = option.value;
			},
			
			// ÈÄâÊã©ÊîØ‰ªòÊñπÂºè
			selectPayment(option) {
				this.selectedPayment = option.value;
			},
			
			// Êèê‰∫§ËÆ¢Âçï
			submitOrder() {
				if (!this.canSubmit) {
					uni.showToast({
						title: 'ËØ∑ÂÆåÂñÑËÆ¢Âçï‰ø°ÊÅØ',
						icon: 'none'
					});
					return;
				}
				
				// ÊûÑÂª∫ËÆ¢ÂçïÊï∞ÊçÆ
				const orderData = {
					orderNo: this.generateOrderNo(),
					items: this.orderItems,
					address: this.selectedAddress,
					delivery: this.deliveryOptions.find(d => d.value === this.selectedDelivery),
					payment: this.paymentOptions.find(p => p.value === this.selectedPayment),
					remark: this.orderRemark,
					totalPrice: this.totalPrice,
					deliveryFee: this.deliveryFee,
					finalPrice: this.finalPrice,
					createTime: new Date().toISOString()
				};
				
				// ÊòæÁ§∫Á°ÆËÆ§ÂºπÁ™ó
				uni.showModal({
					title: 'Á°ÆËÆ§ËÆ¢Âçï',
					content: `ËÆ¢ÂçïÊÄªÈáëÈ¢ùÔºö¬•${this.finalPrice}\nÊîØ‰ªòÊñπÂºèÔºö${orderData.payment.name}\nÈÖçÈÄÅÊñπÂºèÔºö${orderData.delivery.name}`,
					success: (res) => {
						if (res.confirm) {
							this.processPayment(orderData);
						}
					}
				});
			},
			
			// ÁîüÊàêËÆ¢ÂçïÂè∑
			generateOrderNo() {
				const timestamp = Date.now();
				const random = Math.floor(Math.random() * 1000);
				return `SHOP${timestamp}${random}`;
			},
			
			// Â§ÑÁêÜÊîØ‰ªò
			processPayment(orderData) {
				uni.showLoading({
					title: 'Â§ÑÁêÜ‰∏≠...'
				});
				
				// Ê®°ÊãüÊîØ‰ªòËøáÁ®ã
				setTimeout(() => {
					uni.hideLoading();
					
					// ‰øùÂ≠òËÆ¢ÂçïÂà∞Êú¨Âú∞
					this.saveOrder(orderData);
					
					// Â¶ÇÊûúÊòØË¥≠Áâ©ËΩ¶ÁªìÁÆóÔºåÊ∏ÖÁ©∫Ë¥≠Áâ©ËΩ¶
					if (this.orderType === 'cart') {
						this.clearCart();
					}
					
					uni.showToast({
						title: 'ËÆ¢ÂçïÊèê‰∫§ÊàêÂäü',
						icon: 'success'
					});
					
					// Ë∑≥ËΩ¨Âà∞ËÆ¢ÂçïËØ¶ÊÉÖÈ°µ
					setTimeout(() => {
						uni.redirectTo({
							url: `/pages/shop/order-detail?orderNo=${orderData.orderNo}`
						});
					}, 1500);
				}, 2000);
			},
			
			// ‰øùÂ≠òËÆ¢Âçï
			saveOrder(orderData) {
				let orders = uni.getStorageSync('shop_orders') || [];
				orders.unshift(orderData);
				uni.setStorageSync('shop_orders', orders);
			},
			
			// Ê∏ÖÁ©∫Ë¥≠Áâ©ËΩ¶
			clearCart() {
				uni.removeStorageSync('cart');
			}
		}
	}
</script>

<style lang="scss">
	.content {
		background-color: #fef8fa; // Êõ¥Ê∑°ÁöÑÊµÖÁ≤âËâ≤ËÉåÊôØ
		min-height: 100vh;
		padding-bottom: 120rpx;
	}
	
	/* ÈÄöÁî®Ê†∑Âºè */
	.section-title {
		font-size: 32rpx;
		font-weight: bold;
		color: #333;
		margin-bottom: 20rpx;
	}
	
	/* Âú∞ÂùÄÂå∫Âüü */
	.address-section {
		background-color: #fff;
		padding: 30rpx;
		margin-bottom: 20rpx;
		
		.address-card {
			background-color: #f8f8f8;
			padding: 30rpx;
			border-radius: 12rpx;
			position: relative;
			
			.address-info {
				display: flex;
				align-items: center;
				gap: 20rpx;
				margin-bottom: 10rpx;
				
				.contact-name {
					font-size: 28rpx;
					font-weight: bold;
					color: #333;
				}
				
