					font-size: 26rpx;
					color: #666;
				}
			}
			
			.address-detail {
				font-size: 26rpx;
				color: #333;
				line-height: 1.5;
			}
			
			.no-address {
				.no-address-text {
					font-size: 28rpx;
					color: #999;
				}
			}
			
			.address-arrow {
				position: absolute;
				right: 30rpx;
				top: 50%;
				transform: translateY(-50%);
				font-size: 28rpx;
				color: #999;
			}
		}
	}
	
	/* ÂïÜÂìÅÂå∫Âüü */
	.products-section {
		background-color: #fff;
		padding: 30rpx;
		margin-bottom: 20rpx;
		
		.product-list {
			.product-item {
				display: flex;
				align-items: center;
				gap: 20rpx;
				padding: 20rpx 0;
				border-bottom: 1rpx solid #f0f0f0;
				
				&:last-child {
					border-bottom: none;
				}
				
				.product-image {
					width: 100rpx;
					height: 100rpx;
					border-radius: 8rpx;
				}
				
				.product-info {
					flex: 1;
					
					.product-name {
						font-size: 28rpx;
						color: #333;
						display: block;
						margin-bottom: 8rpx;
					}
					
					.product-spec {
						font-size: 24rpx;
