						align-items: center;
						
						.product-price {
							font-size: 28rpx;
							font-weight: bold;
							color: #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
						}
						
						.product-quantity {
							font-size: 24rpx;
							color: #999;
						}
					}
				}
			}
		}
	}
	
	/* ÈÖçÈÄÅÊñπÂºè */
	.delivery-section {
		background-color: #fff;
		padding: 30rpx;
		margin-bottom: 20rpx;
		
		.delivery-options {
			.delivery-option {
				display: flex;
				justify-content: space-between;
				align-items: center;
				padding: 20rpx 0;
				border-bottom: 1rpx solid #f0f0f0;
				
				&:last-child {
					border-bottom: none;
				}
				
				&.active {
					.delivery-name {
						color: #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
					}
					
					.delivery-fee {
						color: #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
					}
				}
				
				.delivery-name {
					font-size: 28rpx;
					color: #333;
				}
				
				.delivery-fee {
					font-size: 26rpx;
					color: #666;
				}
			}
		}
	}
	
	/* ÊîØ‰ªòÊñπÂºè */
	.payment-section {
		background-color: #fff;
		padding: 30rpx;
		margin-bottom: 20rpx;
		
		.payment-options {
			.payment-option {
				display: flex;
				align-items: center;
				gap: 20rpx;
				padding: 20rpx 0;
				border-bottom: 1rpx solid #f0f0f0;
				
				&:last-child {
					border-bottom: none;
				}
				
				&.active {
					.payment-name {
						color: #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
					}
					
					.payment-arrow {
						color: #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
					}
				}
				
				.payment-icon {
					font-size: 32rpx;
				}
				
				.payment-name {
					flex: 1;
					font-size: 28rpx;
					color: #333;
				}
				
				.payment-arrow {
					font-size: 28rpx;
					color: #ccc;
					
					&.selected {
						color: #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
					}
				}
			}
		}
	}
	
	/* ËÆ¢ÂçïÂ§áÊ≥® */
	.remark-section {
		background-color: #fff;
		padding: 30rpx;
		margin-bottom: 20rpx;
		
		.remark-input {
			width: 100%;
			height: 120rpx;
			background-color: #f8f8f8;
			border-radius: 8rpx;
			padding: 20rpx;
			font-size: 26rpx;
			box-sizing: border-box;
		}
	}
	
	/* ‰ª∑Ê†ºÊòéÁªÜ */
	.price-section {
		background-color: #fff;
		padding: 30rpx;
		
		.price-list {
			.price-item {
				display: flex;
				justify-content: space-between;
				align-items: center;
				margin-bottom: 15rpx;
				font-size: 28rpx;
				color: #666;
				
				&.total {
					margin-top: 20rpx;
					padding-top: 20rpx;
					border-top: 1rpx solid #f0f0f0;
					font-size: 32rpx;
					font-weight: bold;
					color: #333;
				}
				
				.price-value {
					color: #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
				}
				
				.final-price {
					color: #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
					font-size: 36rpx;
				}
			}
		}
	}
	
	/* Â∫ïÈÉ®ÁªìÁÆóÊ†è */
	.bottom-bar {
		position: fixed;
		bottom: 0;
		left: 0;
		right: 0;
		background-color: #fff;
		padding: 20rpx 30rpx;
		border-top: 1rpx solid #f0f0f0;
		display: flex;
		align-items: center;
		justify-content: space-between;
		
		.price-summary {
			display: flex;
			align-items: center;
			
			.summary-label {
				font-size: 28rpx;
				color: #666;
			}
			
			.summary-price {
				font-size: 36rpx;
				font-weight: bold;
				color: #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
			}
		}
		
		.pay-btn {
			background-color: #FFB6C1; // Êõ¥Ê∑°ÁöÑÁ≤âËâ≤
			color: #fff;
			padding: 20rpx 60rpx;
			border-radius: 40rpx;
			font-size: 30rpx;
			font-weight: bold;
			
			&:disabled {
				background-color: #ccc;
			}
		}
	}
</style> 